<template>
  <div>
    <NotAuth>
      <template v-slot:error></template>
      <template v-slot:loading></template>
      <template v-slot:default>
        <div class="chat__wrapper d-flex flex-row" @click="isModal = false">
          <div style="margin-top: 80px; margin-left: 20px;">
            <Panel />
          </div>
          <v-card class="chat">
            <div class="d-flex align-center">
              <v-card-title class="chat__title">Мои чаты</v-card-title>
              <v-spacer></v-spacer>
              <v-btn 
                color="myPurple" 
                @click.stop="isModal = true" 
                style="margin-right: 20px; color: #fff"
              >
                Создать чат
              </v-btn>
            </div>
            <div v-if="chats != []">
              <div
                class="chat__info d-flex justify-space-between"
                v-for="(chat, index) in chats"
                :key="index"
                @click="$router.push({ name: 'Chat', params: { id: chat.chatId }})"
              >
                <div 
                  class="d-flex flex-row align-center" 
                >
                  <div class="chat__info__image" v-if="chat.img">
                    {{ chat.img }}
                  </div>
                  <div class="chat__info__image" v-else></div>
                  <div class="chat__info__name">
                    <v-card-subtitle class="chat__info__title">
                      {{ chat.title }}
                    </v-card-subtitle>
                    <v-card-text class="chat__info__text">{{ chat.lastMsg }}</v-card-text>
                  </div>
                </div>
                <v-card-subtitle class="chat__info__right">{{ chat.dateMsg }}</v-card-subtitle>
              </div>
            </div>
            <div v-else class="chat__info__undefined">
              <h2>Вы не состоите не в одном чате.</h2>
            </div>
          </v-card>
          <ChatCreate :isModal="isModal" @isModalFalse="isModalFalse" />
        </div>
      </template>
    </NotAuth>
  </div>
</template>

<script>
import ChatCreate from "../components/chat/chatCreate.vue";
import Panel from "../components/Panel.vue";
import NotAuth from "../components/NotAuth.vue";
import axios from "axios";
import "../scss/chats.scss";

export default {
  name: "Chats",
  metaInfo: {
    title: "MyOcean - Мои чаты"
  },
  components: { ChatCreate, Panel, NotAuth },
  data() {
    return {
      dataDB: "",
      text: "",
      isModal: false,
      chats: [],
    };
  },
  async created() {
    this.dataDB = JSON.parse(localStorage.getItem("user"))
    let res = await axios.get("/api/chat/get-chats/" + this.dataDB.id)
    this.chats = res.data
  },
  sockets: {
    connect: function() {
      console.log("конэктэд")
    },
  },
  methods: {
    isModalFalse(data) {
      this.isModal = data
    }
  }
};
</script>
