<template>
  <div>
    <slot name="error" v-if="err">
      <div class="auth__error d-flex flex-row align-center">
        <div>
          <h2>Вы не можете просматривать данную страницу ( 401 )</h2>
          <p>
            Для просмотра вы должны
            <router-link class="auth__error__link" to="/">
              зарегистрироваться
            </router-link>
          </p>
        </div>
        <v-img max-width="256" height="256" src="../assets/covid.png"></v-img>
      </div>
    </slot>
    <slot name="loading" v-else-if="loading">
      <Loader />
    </slot>
    <slot v-else>profit</slot>
  </div>
</template>

<script>
import Loader from "../components/Loader.vue";
import axios from "axios";

export default {
  name: "NotAuth",
  components: { Loader },
  data() {
    return {
      err: false,
      loading: true,
    };
  },
  async beforeCreate() {
    try {
      await axios.get("/api/auth/test", 
        { headers: { Authorization: localStorage.getItem("jwt") }});
      this.loading = false;
    } catch (error) {
      this.err = true;
      this.loading = false;
    }
  },
};
</script>

<style lang="scss" scoped>
.auth__error {
  margin-left: 50px;
  margin-top: 150px;
  h2 {
    width: 700px;
    font-size: 40px;
  }
  p {
    margin-top: 30px;
    font-size: 24px;
  }
  &__link {
    text-decoration: none;
    color: #7f5af0;
    &:hover {
      text-decoration: underline;
    }
  }
}
</style>
