<template>
  <div>
    <v-dialog
      v-model="isModal"
      max-width="700"
      persistent
    >
      <v-card>
        <v-toolbar color="myPurple">
          <h2 style="color: #fff">Создать чат</h2>
          <v-spacer></v-spacer>
          <v-icon size="32" style="cursor: pointer" @click="$emit('isModalFalse', false)">
            cancel
          </v-icon>
        </v-toolbar>
        <div class="chat__modal__middle d-flex flex-column align-center">
          <v-card-title>
            Выберите человека, с которым вы хотите по общаться
          </v-card-title>
          <v-text-field 
            @keydown.enter="dataSend" 
            v-model="fullName" 
            solo 
            label="Искать" 
            class="chat__modal__input"
          >  
          </v-text-field>
          <div v-if="friends">
            <div class="chat__modal__item" v-for="(friend, index) of friends" :key="index">
              <div class="d-flex flex-row">
                <v-img 
                  class="chat__modal__img"
                  v-if="friend.additionally.img" 
                  :src="'/' + friend.additionally.img"
                ></v-img>
                <div v-else class="chat__modal__img"></div>
                <div class="d-flex flex-column">
                  <router-link 
                    :to="{ name: 'Account', params: { id: friend.id }}" 
                    class="chat__modal__link"
                  >
                    {{ friend.firstname }} {{ friend.lastname }}
                  </router-link>
                  <v-btn 
                    @click="chatCreate(friend)" 
                    class="chat__modal__btn" 
                    color="myPurple"
                  >
                    Написать
                  </v-btn>
                </div>
              </div>
            </div>
          </div>
        </div>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "ChatCreate",
  data() {
    return {
      friends: "",
      user: "",
      fullName: "",
      firstname: "",
      lastname: ""
    }
  },
  props: {
    isModal: Boolean
  },
  created() {
    this.user = JSON.parse(localStorage.getItem("user"))
    // let res = await axios.get("/api/users/get-friends/" + this.user.id)
    // this.friends = res.data
    // console.log(res.data)
  },
  methods: {
    async dataSend() {
      this.firstname = this.fullName.trimLeft().trimRight().split(" ")[0]
      this.lastname = this.fullName.trimLeft().trimRight().split(" ")[1]
      let res = await axios.post("/api/users/get_friends/",
        { firstname: this.firstname, lastname: this.lastname })
      this.friends = res.data
    },
    async chatCreate(friend) {
      let res = await axios.post("/api/chat/chat-create", 
        { userId: friend.id, myId: this.user.id })
      this.$router.push({ name: 'Chat', params: { id: res.data.chatId }})
    }
  }
};
</script>

<style lang="scss" scoped>
.chat {
  &__modal {
    &__input {
      max-width: 600px;
    }
    &__link {
      text-decoration: none;
      color: #7f5af0;
      margin-left: 20px;
      font-size: 24px;
      &:hover {
        text-decoration: underline;
      }
    }
    &__img {
      max-width: 128px;
      max-height: 128px;
      min-width: 128px;
      min-height: 128px;
      border-radius: 50%;
      background-color: #2cb67d;
      cursor: pointer;
    }
    &__btn {
      width: 200px;
      margin-left: 40px;
      margin-top: 20px;
      color: #fff;
    }
    &__item {
      margin-top: 30px;
      &:last-child {
        margin-bottom: 30px;
      }
    }
  }
}
</style>
